import React from 'react'

const Header = ({score}) => {
    return (
        <div className="header">
            <div className="text">
                <span>
                    Rock
                </span>
                <span>
                    Paper
                </span>
                <span>
                    Scissors
                </span>
            </div>
            <div className="score-box">
                <span>Score </span>
                <div className="score-box__score">
                <span>{score}</span>
                </div>
            </div>
             
        </div>
    )
}

export default Header
